* { 
  -webkit-tap-highlight-color:rgba(0,0,0,0); 
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body:before {
  content: "";
  display: block;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -10;
  background: url(/frame-react-home/static/media/6f6ccb4c.dac30a96.png) no-repeat center center;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: Futura, sans-serif;
  color: white;
  z-index: 1;
  /* height: 300vh; */
}

.landingContent {
  height: 100vh;
  width: 100vw;
}

.logoContainer {
  position: absolute;
  width: 20em;
  top: 50px;
  left: 0;
  right: 0;
  margin: auto;
}

.centerLogo {
  width: 20em;
}

.logoBreak {
  margin: auto;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  margin-top: -2.3em;
  width: 19em;
  border: 2px solid white;
}

.logoSub {
  width: 18em;
