}

.logoSub h2 {
  margin-top: -1em
}

.landingButtons {
  position: absolute;
  left: 0;
  right: 0;
  top: 400px;
  margin: auto;
  /* margin-top: 6em; */
}

.signUpButton {
  margin: 15px auto;
  border-radius: .4em;
  width: 13em;
  border: 4px solid white;
}

.downPoint {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 3em;
}

.particleScreen {
  position:fixed;
  width: 100% !important;
  height: 100vmax !important;
  z-index: 2;  
  top:0;
  left:0;
  pointer-events: all;
}

.stickyContent {
  height: 120vh;
}

.stickyWindow {
  overflow: hidden;
  width: 100%;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
  height: 100vh;
}

@-webkit-keyframes slide {
  0%{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  100%{
    -webkit-transform: translate3d(0, 1692px, 0);
            transform: translate3d(0, 1692px, 0); /* The image width */
  }
}

@keyframes slide {
  0%{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  100%{
    -webkit-transform: translate3d(0, 1692px, 0);
            transform: translate3d(0, 1692px, 0); /* The image width */
  }
}


